.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 200px;
  height: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 100%
}


.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  max-width: 1100px;
  margin: 0 auto;
}

.title {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  width: 100%;
  text-align: center;
  font-size: 2rem;
  color: black;
  margin-bottom: 5px;
}

nav {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    background-color: black;
    color: #fff;
    width: 100%;
    font-size: 1rem;
    margin-bottom: 5px;
    height: auto;
  }
  
  a {
    color: white;
  }
  
  a:hover {
    color: white;
    text-decoration: none;
  }
  
  li {
    display: inline-block;
    padding: 12px 0;
    width: 25%;
  }
  
  ul {
    display: flex;
    align-items: center;
  }
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

